import React, { useState, useEffect } from 'react';  // Import useState and useEffect from React
import Web3 from 'web3';  // Import Web3
import Particles from 'react-tsparticles';
import { loadFull } from 'tsparticles';
import './Home.css';  // Import the CSS file

const Home = () => {
    const [account, setAccount] = useState(localStorage.getItem('account') || null);
    const [web3, setWeb3] = useState(null);

    useEffect(() => {
        if (window.ethereum) {
            const web3Instance = new Web3(window.ethereum);
            setWeb3(web3Instance);
        } else {
            alert('Please install MetaMask!');
        }
    }, []);

    const connectWallet = async () => {
        try {
            const accounts = await window.ethereum.request({ method: 'eth_requestAccounts' });
            setAccount(accounts[0]);
            localStorage.setItem('account', accounts[0]);  // Store account in localStorage
        } catch (error) {
            console.error('Error connecting to MetaMask:', error);
        }
    };

    const logout = () => {
        setAccount(null);
        localStorage.removeItem('account');  // Remove account from localStorage
    };

    // Particle animation configuration
    const particlesInit = async (main) => {
        await loadFull(main);
    };

    return (
        <div className="home-container">
            <div className="logout-button-container">
                {account && (
                    <button onClick={logout} className="logout-button">
                        Logout
                    </button>
                )}
            </div>

            {account ? (
                <h1 className="welcome-message">Welcome, {account}</h1>
            ) : (
                <div className="connect-container">
                    <button onClick={connectWallet} className="connect-button">
                        Connect MetaMask Wallet
                    </button>
                </div>
            )}

            {/* Adding animated particles */}
            <Particles
                id="tsparticles"
                init={particlesInit}
                options={{
                    background: {
                        color: {
                            value: "#1e1e2c",
                        },
                    },
                    fpsLimit: 60,
                    particles: {
                        color: {
                            value: "#ffffff",
                        },
                        links: {
                            color: "#ffffff",
                            distance: 150,
                            enable: true,
                            opacity: 0.5,
                            width: 1,
                        },
                        collisions: {
                            enable: true,
                        },
                        move: {
                            directions: "none",
                            enable: true,
                            outMode: "bounce",
                            random: false,
                            speed: 2,
                            straight: false,
                        },
                        number: {
                            density: {
                                enable: true,
                                area: 800,
                            },
                            value: 80,
                        },
                        opacity: {
                            value: 0.5,
                        },
                        shape: {
                            type: "circle",
                        },
                        size: {
                            random: true,
                            value: 5,
                        },
                    },
                    detectRetina: true,
                }}
            />
        </div>
    );
};

export default Home;
